.navbar
{
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.navbar .logo
{
    width: 150px;
}

.navbar-menu
{
    list-style: none;
    display: flex;
    gap: 20px;
   
    color:#49557e;
    font-size: 18px;
}

.navbar-right{

    display: flex;
    gap: 20px;
    align-items: center;
}
.navbar-right button
{
    background-color: transparent;
    font-size: 16px;
    border-radius: 50px;
    border: 1px solid tomato;
    padding: 10px 30px ;
    cursor: pointer;
    color:#49557e;
}

.navbar-right button:hover
{
    background-color: #fff4f2;
    transition: 0.3s;
}

.navbar .active
{
    padding-bottom: 2px;
    border-bottom:2px solid #49557e;
}

.navbar-menu li{
    cursor: pointer;
}

.navbar-search-icon
{
    position: relative;
}
.navbar-search-icon .dot
{
  position:absolute;
  min-width: 10px;
  min-height: 10px;
  background-color: tomato;
  border-radius: 5px;
  top:-8px;
  right: -8px;
    
}
.search-icon
{
    cursor: pointer;
}
.navbar-profile
{
    position: relative;
}
.navbar-profile-dropdown
{
    position: absolute;
    display: none;
    right: 0;
    z-index: 1;

}

.navbar-profile:hover .navbar-profile-dropdown
{
    display: flex;
    flex-direction: column;
    gap:10px;
    background-color: #fff2ef;
    padding: 12px 25px;
    border-radius: 4px;
    border: 1px solid tomato;
    outline: 2px solid white;
    list-style: none;
}
.navbar-profile-dropdown li
{
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}
.navbar-profile-dropdown img
{
    width: 20px;
}
.navbar-profile-dropdown li:hover
{
    color: tomato;
}

@media (max-width:1050px)
{
    .navbar .logo{
        width: 140px;
    }

    .navbar-menu
    {
        gap:20px;
        font-size: 17px;

    }
    .navbar-right
    {
        gap: 30px;
    }
    .navbar-right img
    {
        width: 22px;
    }

    .navbar-right button
    {
        padding: 8px 25px;
    }
}

@media (max-width:900px)
{
    .navbar .logo{
        
        width: 120px;
    }

    .navbar-menu
    {
        gap:15px;
        font-size: 16px;

    }
    .navbar-right
    {
        gap: 20px;
    }
    .navbar-right img
    {
        width: 20px;
    }

    .navbar-right button
    {
        padding: 7px 20px;
        font-size: 15px;
    }
}

@media (max-width:750px)
{
   

    .navbar-menu
    {
        display: none;

    }
    
}